import React, { Component } from "react";

class Test extends Component {
  constructor() {
    super();
    this.state = {
      title: "",
      body: ""
    };
  }
  componentDidMount() {
    console.log("did mount");
    fetch("https://jsonplaceholder.typicode.com/todos/1")
      .then(response => response.json())
      .then(data =>
        this.setState({
          title: data.title,
          body: "data loaded"
        })
      );
  }

  render() {
    const { title, body } = this.state;
    return (
      <div>
        <h1>{title}</h1>
        <p>{body}</p>
      </div>
    );
  }
}
export default Test;
